
<template>

    <div class="feedback">

        <div class="back"></div>

        <div class="wrap">

            <div class="top">
                <div class="name"> -</div>
            </div>

            <div class="cnt">

                <div class="wrap input">
                    <label for="input-180"></label>
                    <input v-model="sub.name" id="input-180" />
                </div>

                <div class="wrap textarea">
                    <label for="input-179"></label>
                    <textarea id="input-179" v-model="sub.pre_content" rows="5"></textarea>
                </div>

                <div class="wrap input">
                    <label for="input-180"> </label>
                    <input v-model="sub.slug" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180">  </label>
                    <input v-model="sub.seo_t" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180">  </label>
                    <input v-model="sub.seo_d" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180"></label>
                    <input v-model="sub.price" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180"></label>
                    <input v-model="sub.discount" id="input-180" />
                </div>

                <div class="wrap checkbox">
                    <input type="checkbox" v-model="sub.price_from" id="input-180" />
                    <label for="input-180"> ?</label>
                </div>

                <div class="wrap input">
                    <label for="input-180"> </label>
                    <input v-model="sub.execution_time" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180">   </label>
                    <input v-model="sub.take" id="input-180" />
                </div>

            </div>

            <div class="btm">
                <div @click="update()" :class="['send', 'active']"></div>
                <router-link :to="{ name: `admin.subs.detail`, }" class="cancel"></router-link>
            </div>

        </div>

    </div>

</template>

<script>

import { computed, onMounted } from "vue"
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import { useRoute } from 'vue-router'

export default {
	setup(){

        const route     = useRoute()
        const router    = useRouter()
		const store     = useStore()
        const sub       = computed(() => store.getters.sub)

        onMounted(async () => await store.dispatch('get_sub', route.params.sd_id))

        async function update(){

            await store.dispatch('update_subs', sub.value)

            router.push({ name: `admin.subs.list` })

        }

		return { update, sub }
	}
}

</script>