                    </div>
                -->

                <div class="wrap input">
                    <label for="input-180"></label>
                    <input v-model="service.name" id="input-180" />
                </div>

                <div class="wrap textarea">
                    <label for="input-180"></label>
                    <textarea v-model.trim="service.pre_content"></textarea>
                </div>
                
                
                <div class="wrap input">
