
<template>

    <div class="feedback">

        <div class="back"></div>

        <div class="wrap">

            <div class="top">
                <div class="name"> </div>
            </div>

            <div class="cnt">
                
                <!--
                    <div class="name">
                                  ,  -   ,  ,    .               .
                    </div>
                -->

                <div class="wrap input">
                    <label for="input-180"></label>
                    <input v-model="service.name" id="input-180" />
                </div>

                <div class="wrap textarea">
                    <label for="input-180"></label>
                    <textarea v-model.trim="service.pre_content"></textarea>
                </div>
                
                
                <div class="wrap input">
                    <label for="input-180"> </label>
                    <input v-model="service.slug" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180"> </label>
                    <input v-model="service.seo_t" id="input-180" />
                </div>

                <div class="wrap input">
                    <label for="input-180"> </label>
                    <input v-model="service.seo_d" id="input-180" />
                </div>

                <!--
                    <div class="desc">     ,  ,       .</div>
                -->

            </div>

            <div class="btm">
                <div @click="add()" :class="['send', {active: true}]"></div>
                <router-link :to="{ name: `admin.services.list`, }" class="cancel"></router-link>
            </div>


        </div>

    </div>

</template>

<script>

import { computed } from "vue"
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'


export default {
	setup(){

        const router    = useRouter()
        const store     = useStore()
        const service 	= computed(() => store.getters.service)


        async function add(){
            
            await store.dispatch('add_services', service.value)

            router.push({ name: `admin.services.list` })
            
        }

        return { service, add }
	}
}

</script>