
<template>

    <div class="feedback">

        <div class="back"></div>

        <div class="wrap">

            <div class="top">
                <div class="name"> </div>
            </div>

            <div class="cnt">
                
                <!--
                    <div class="name">
                                  ,  -   ,  ,    .               .
                    </div>
                -->

                <div class="wrap input">
                    <label for="input-180"></label>
                    <input v-model="guarantee.name" id="input-180" />
                </div>
                
                <div class="wrap input">
                    <label for="input-180"></label>
                    <input v-model="guarantee.price" id="input-180" />
                </div>

                <div class="wrap checkbox">
                    <input type="checkbox" v-model="guarantee.default" id="input-180" />
                    <label for="input-180"> ?</label>
                </div>

                <!--
                    <div class="desc">     ,  ,       .</div>
                -->

            </div>

            <div class="btm">
                <div @click="update()" :class="['send', {active: true}]">update</div>
                <router-link :to="{ name: `admin.garantee`, }" class="cancel"></router-link>
            </div>


        </div>

    </div>

</template>

<script>

import { computed, onMounted } from "vue"
import { useStore } from 'vuex'

import { useRouter } from 'vue-router'
import { useRoute } from 'vue-router'


export default {
	setup(){

        const router        = useRouter()
        const route         = useRoute()
        const store         = useStore()
        const guarantee     = computed(() => store.getters.guarantee)

        onMounted(async () => await store.dispatch('get_guarantee', route.params.id))


        async function update(){

            await store.dispatch('update_guarantees', guarantee.value)

            router.push({ name: `admin.garantee` })

        }

        return { guarantee, update }
        
	}
}

</script>