
<template>

    <div class="feedback">

        <div class="back"></div>

        <div class="wrap">

            <div class="top">
                <div class="name"> </div>
            </div>

            <div class="cnt">
                
                <!--
                    <div class="name">
                                  ,  -   ,  ,    .               .
                    </div>
                -->

                <div class="wrap input">
                    <label for="input-180"></label>
                    <input v-model="guarantee.name" id="input-180" />
                </div>
                
                <div class="wrap input">
                    <label for="input-180"></label>
                    <input v-model="guarantee.price" id="input-180" />
                </div>

                <div class="wrap checkbox">
                    <input type="checkbox" v-model="guarantee.default" id="input-180" />
                    <label for="input-180"> ?</label>
                </div>

                <!--
                    <div class="desc">     ,  ,       .</div>
                -->

            </div>

            <div class="btm">
                <div @click="add()" :class="['send', {active: true}]"></div>
                <router-link :to="{ name: `admin.garantee`, }" class="cancel"></router-link>
            </div>


        </div>

    </div>

</template>

<script>

import { computed } from "vue"
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'

export default {
	setup(){

        const router    = useRouter()
        const store     = useStore()
        const guarantee = computed(() => store.getters.guarantee)


        async function add(){
            
            await store.dispatch('add_guarantees', guarantee.value)

            router.push({ name: `admin.garantee` })
            
        }

        return { guarantee, add }

	}
}

</script>